import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imageviper from '../../Images/Viper_artwork.webp';

function Viper() {
    return(
        <div className="main">
            <div className='main-2'>Viper</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageviper} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    The American chemist, Viper deploys an array of poisonous chemical devices to control the battlefield and cripple the enemy's vision. If the toxins don't kill her prey, her mind games surely will.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Poison Cloud', cost: '200 Creds' },
                                    {ability: 'Toxic Screen', cost: 'Free per round'},
                                    {ability: 'Snake Bite', cost: '100 Creds' },
                                    {ability: 'Viper’s Pit', cost: '7 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Viper;