import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imageomen from '../../Images/Omen_artwork.webp';

function Omen() {
    return(
        <div className="main">
            <div className='main-2'>Omen</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageomen} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    A phantom of a memory, Omen hunts in the shadows. He renders enemies blind, teleports across the field, then lets paranoia take hold as his foe scrambles to learn where he might strike next.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Paranoia', cost: '200 Creds' },
                                    {ability: 'Dark Cover', cost: '2 Free per round'},
                                    {ability: 'Shrouded Step', cost: '100 Creds' },
                                    {ability: 'From The Shadows', cost: '7 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Omen;