import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imagebreach from '../../Images/Breach_artwork.webp';

function Breach() {
    return(
        <div className="main">
            <div className='main-2'>Breach</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagebreach} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Breach, the bionic Swede, fires powerful, targeted kinetic blasts to aggressively clear a path through enemy ground. The damage and disruption he inflicts ensures no fight is ever fair.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Flashpoint', cost: '100 Creds' },
                                    {ability: 'Fault Line', cost: 'Free with 35s cooldown)'},
                                    {ability: 'Aftershock', cost: '100 Creds' },
                                    {ability: 'Rolling Thunder', cost: 0}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Breach;