import React, {Component} from 'react';
import './Weapons.css';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu } from 'react-mdl';

import Ares from './Images/Ares.webp';
import Bucky from './Images/Bucky.webp';
import Bulldog from './Images/Bulldog.webp';
import Classic from './Images/Classic.webp';
import Frenzy from './Images/Frenzy.webp';
import Ghost from './Images/Ghost.webp';
import Guardian from './Images/Guardian.webp';
import Judge from './Images/Judge.webp';
import Marshal from './Images/Marshal.webp';
import Odin from './Images/Odin.webp';
import Operator from './Images/Operator.webp';
import Phantom from './Images/Phantom.webp';
import Sheriff from './Images/Sheriff.webp';
import Shorty from './Images/Shorty.webp';
import Spectre from './Images/Spectre.webp';
import Stinger from './Images/Stinger.webp';
import TacticalKnife from './Images/TacticalKnife.webp';
import Vandal from './Images/Vandal.webp';

class AllWeapons extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
            if(this.state.activeTab === 0){
                return(
                <div>
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Classic} style={{maxWidth:'80%'}} alt="Classic"/></CardTitle>
                                <CardText>Classic</CardText>
                                <CardActions border>
                                    <Button colored>Default</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Shorty} style={{maxWidth:'80%'}} alt="Shorty"/></CardTitle>
                                <CardText>Shorty</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  200 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Frenzy} style={{maxWidth:'80%'}} alt="Frenzy"/></CardTitle>
                                <CardText>Frenzy</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  400 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                    <div className="projects-grid-2">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Ghost} style={{maxWidth:'80%'}} alt="Ghost"/></CardTitle>
                                <CardText>Ghost</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  500 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Sheriff} style={{maxWidth:'80%'}} alt="Sheriff"/></CardTitle>
                                <CardText>Sheriff</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  800 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        </div>
                    </div>
                )
            }
            else if(this.state.activeTab === 1){
                return(
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Stinger} style={{maxWidth:'80%'}} alt="Stinger"/></CardTitle>
                                <CardText>Stinger</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  1,000 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Spectre} style={{maxWidth:'80%'}} alt="Spectre"/></CardTitle>
                                <CardText>Spectre</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  1,600 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }      
            else if(this.state.activeTab === 2){
                return(
                    <div>
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Bulldog} style={{maxWidth:'80%'}} alt="Bulldog"/></CardTitle>
                                <CardText>Bulldog</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  2,100 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Guardian} style={{maxWidth:'80%'}} alt="Guardian"/></CardTitle>
                                <CardText>Guardian</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  2,700 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Phantom} style={{maxWidth:'80%'}} alt="Phantom"/></CardTitle>
                                <CardText>Phantom</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  2,900 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        </div>
                        <div className="projects-grid-2">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Vandal} style={{maxWidth:'80%'}} alt="Vandal"/></CardTitle>
                                <CardText>Vandal</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  2,900 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                    </div>
                )
            }
            else if(this.state.activeTab === 3){
                return(
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Bucky} style={{maxWidth:'80%'}} alt="Bucky"/></CardTitle>
                                <CardText>Bucky</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  900 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Judge} style={{maxWidth:'80%'}} alt="Judge"/></CardTitle>
                                <CardText>Judge</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  1,500 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
            else if(this.state.activeTab === 4){
                return(
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Ares} style={{maxWidth:'80%'}} alt="Ares"/></CardTitle>
                                <CardText>Ares</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  1,600 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Odin} style={{maxWidth:'80%'}} alt="Odin"/></CardTitle>
                                <CardText>Odin</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  3,200 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
            else if(this.state.activeTab === 5){
                return(
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Marshal} style={{maxWidth:'80%'}} alt="Marshal"/></CardTitle>
                                <CardText>Marshal</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  1,100 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={Operator} style={{maxWidth:'80%'}} alt="Operator"/></CardTitle>
                                <CardText>Operator</CardText>
                                <CardActions border>
                                    <Button colored>Cost :  4,500 Creds</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
            else if(this.state.activeTab === 6){
                return(
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={TacticalKnife} style={{maxWidth:'80%'}} alt="TacticalKnife"/></CardTitle>
                                <CardText>Tactical Knife</CardText>
                                <CardActions border>
                                    <Button colored>Default</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
    }

    render() { 
        return (
        <div className="category-tabs">
            <Tabs activeTab={ this.state.activeTab } onChange={(tabID) => this.setState({ activeTab: tabID })} ripple>
                <Tab>Sidearms</Tab>
                <Tab>SMGs</Tab>
                <Tab>Rifles</Tab>
                <Tab>Shotguns</Tab>
                <Tab>Machine Guns</Tab>
                <Tab>Sniper Rifles</Tab>
                <Tab>Melee</Tab>
            </Tabs>

            <section className='allmapbg'>
                    <Grid>
                        <Cell col={12}><div className="content">{this.toggleCategories()}</div></Cell>
                    </Grid>             
            </section>
        </div>
        );
    }
}

export default AllWeapons;