import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imagesage from '../../Images/Sage_artwork.webp';

function Sage() {
    return(
        <div className="main">
            <div className='main-2'>Sage</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagesage} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    The stronghold of China, Sage creates safety for herself and her team wherever she goes. Able to revive fallen friends and stave off aggressive pushes, she provides a calm center to a hellish fight.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Slow Orb', cost: '100 Creds' },
                                    {ability: 'Healing Orb', cost: 'Free with 35s cooldown'},
                                    {ability: 'Barrier Orb', cost: '400 Creds' },
                                    {ability: 'Resurrection', cost: '7 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Sage;