import React from 'react';
import './MapsInfo.css';
import minimap from './Images/Haven_mini-map.webp';

function HavenInfo() {
    return(
        <div className='maps'>
            Beneath a forgotten monastery, a clamour emerges from rival Agents clashing to control three sites. There’s more territory to control, but defenders can use the extra real estate for aggressive pushes.
            <div className='minimap'><img src={minimap} style={{maxWidth:'50%'}} alt="minimap"/>
            </div>
        </div>
    )
}

export default HavenInfo;