import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imagecypher from '../../Images/Cypher_artwork.webp';

function Cypher() {
    return(
        <div className="main">
            <div className='main-2'>Cypher</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagecypher} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    The Moroccan information broker, Cypher is a one-man surveillance network who keeps tabs on the enemy's every move. No secret is safe. No maneuver goes unseen. Cypher is always watching.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Cyber Cage', cost: '100 Creds' },
                                    {ability: 'Spycam', cost: 'Free per round'},
                                    {ability: 'Trapwire', cost: '200 Creds' },
                                    {ability: 'Neural Theft', cost: '7 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Cypher;