import React from 'react';
import './MapsInfo.css';
import minimap from './Images/Range_mini-map.webp';

function RangeInfo() {
    return(
        <div className='maps'>
           The Range is a practice map where players can test their skills with the parkour trial event, test different abilities on every Agent and try out different types of weapons. 
        <div className='minimap'><img src={minimap} style={{maxWidth:'50%'}} alt="minimap"/>
            </div>
        </div>
    )
}

export default RangeInfo;