import React,{Component} from 'react';
import './Agents.css';

import Jett from './AgentInfo/Duelist/Jett';
import Phoenix from './AgentInfo/Duelist/Phoenix';
import Raze from './AgentInfo/Duelist/Raze';
import Reyna from './AgentInfo/Duelist/Reyna';

import Sova from './AgentInfo/Initiator/Sova';
import Breach from './AgentInfo/Initiator/Breach';
import Skye from './AgentInfo/Initiator/Skye';

import Cypher from './AgentInfo/Sentinal/Cypher';
import Killjoy from './AgentInfo/Sentinal/Killjoy';

import Omen from './AgentInfo/Controller/Omen';
import Sage from './AgentInfo/Controller/Sage';
import Brimstone from './AgentInfo/Controller/Brimstone';
import Viper from './AgentInfo/Controller/Viper';

import imagebreach from './Images/Breach_artwork.webp';
import imagebrimstone from './Images/Brimstone_artwork.webp';
import imagecypher from './Images/Cypher_artwork.webp';
import imagejett from './Images/Jett_artwork.webp';
import imagekilljoy from './Images/Killjoy.webp';
import imageomen from './Images/Omen_artwork.webp';
import imagephoenix from './Images/Phoenix_artwork.webp';
import imageraze from './Images/Raze_artwork.webp';
import imagereyna from './Images/Reyna_artwork.webp';
import imagesage from './Images/Sage_artwork.webp';
import imageskye from './Images/Skye_Keyart_final.webp';
import imagesova from './Images/Sova_artwork.webp';
import imageviper from './Images/Viper_artwork.webp';

import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText } from 'react-mdl';


class AllAgents extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
            if(this.state.activeTab === 0){
                return(
                    <div>
                        <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagejett} style={{maxWidth:'80%'}} alt="imagejett"/></CardTitle>
                            <CardText>JETT</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', margin:'auto'}}><img src={imagephoenix} style={{maxWidth:'80%'}} alt="imagephoenix"/></CardTitle>
                                <CardText>PHOENIX</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', margin:'auto'}}><img src={imagereyna} style={{maxWidth:'80%'}} alt="imagereyna"/></CardTitle>
                                <CardText>REYNA</CardText>
                        </Card>
                        </div>
                        <div className="projects-grid-2">

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', margin:'auto'}}><img src={imageraze} style={{maxWidth:'80%'}} alt="imageraze"/></CardTitle>
                                <CardText>RAZE</CardText>
                        </Card>
                        </div>
                            <Jett />
                            <Phoenix />
                            <Reyna />
                            <Raze />
                    </div>
                )
            }
            else if(this.state.activeTab === 1){
                return(
                    <div>
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageviper} style={{maxWidth:'80%'}} alt="imageviper"/></CardTitle>
                                <CardText>VIPER</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageomen} style={{maxWidth:'80%'}} alt="imageomen"/></CardTitle>
                                <CardText>OMEN</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagebrimstone} style={{maxWidth:'80%'}} alt="imagebrimstone"/></CardTitle>
                                <CardText>BRIMSTONE</CardText>
                        </Card>
                        </div>
                        <div className="projects-grid-2">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagesage} style={{maxWidth:'80%'}} alt="imagesage"/></CardTitle>
                                <CardText>SAGE</CardText>
                        </Card>
                    </div>
                    <Viper />
                    <Omen />
                    <Brimstone />
                    <Sage />
                    </div>
                )
            }      
            else if(this.state.activeTab === 2){
                return(
                    <div>
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagesova} style={{maxWidth:'80%'}} alt="imagesova"/></CardTitle>
                                <CardText>SOVA</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageskye} style={{maxWidth:'80%'}} alt="imageskye"/></CardTitle>
                                <CardText>SKYE</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagebreach} style={{maxWidth:'80%'}} alt="imagebreach"/></CardTitle>
                                <CardText>BREACH</CardText>
                        </Card>
                    </div>
                    <Sova />
                    <Skye />
                    <Breach />
                    </div>
                )
            }
            else if(this.state.activeTab === 3){
                return(
                <div>
                    <div className="projects-grid-1">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagekilljoy} style={{maxWidth:'80%'}} alt="imagekilljoy"/></CardTitle>
                                <CardText>KILLJOY</CardText>
                        </Card>

                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagecypher} style={{maxWidth:'80%'}} alt="imagecypher"/></CardTitle>
                                <CardText>CYPHER</CardText>
                        </Card>
                    </div>
                    <Killjoy />
                    <Cypher />
                </div>
                )
            }
            
    }

    render() { 
        return (
        <div className="category-tabs">
            <Tabs activeTab={ this.state.activeTab } onChange={(tabID) => this.setState({ activeTab: tabID })} ripple>
                <Tab>Duelist</Tab>
                <Tab>Controller</Tab>
                <Tab>Initiator</Tab>
                <Tab>Sentinel</Tab>
            </Tabs>

            <section className="allagentsbg">
                    <Grid>
                        <Cell col={12}><div className="content">{this.toggleCategories()}</div></Cell>
                    </Grid>             
            </section>
        </div>
        );
    }
}

export default AllAgents;