import React from 'react';
import './BattlePass.css';

function BattlePass() {
    return(
        <div>
            <h3>Battle Pass</h3>
            <div className='battlepass'>
                <h5>Battle Pass is a form of progression in Valorant.</h5>
                <h5>A Battle Pass allows players to progress through tiers and earn certain player cards, player titles, weapon charms, sprays and weapon skins.</h5>
                <div className='episode'>
                    Episodes/Acts/Chapters
                    <h6>Each Episode includes three Acts, and for every Act, you get a new Battle Pass. About every two months, an Act ends along with its associated Battle Pass. </h6>
                    <h6>The Battle Pass is broken up into 10 Chapters, where each Chapter contains 5 Premium tiers and rewards a free Chapter completion reward when unlocked. A Chapter is completed when all 5 Premium tiers have been unlocked with XP. Once completed, you’ll earn the Free Chapter Completion Rewards and move on to the next Chapter. </h6>
                    <h6>You can progress through all 10 Chapters without going Premium, however, you won’t unlock the 5 items within the Premium tiers. You can always go Premium and retroactively unlock the rewards you earned while on the free path.</h6>
                </div>
            </div>
        </div>
    );
}

export default BattlePass;