import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imageraze from '../../Images/Raze_artwork.webp';

function Raze() {
    return(
        <div className="main">
            <div className='main-2'>Raze</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageraze} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Raze explodes out of Brazil with her big personality and big guns. With her blunt-force-trauma playstyle, she excels at flushing entrenched enemies and clearing tight spaces with a generous dose of “boom.”
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Blast Pack', cost: '100 Creds'},
                                    {ability: 'Paint Shells', cost: 'Free per round'},
                                    {ability: 'Boom Bot', cost: '200 Creds'},
                                    {ability: 'Showstopper', cost: '6 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Raze;