import React from 'react';
import "./Box.css";
import GlitchClip from 'react-glitch-effect/core/Clip';

function Box() {
    return(
        <div>
            <GlitchClip onHover={true}>
                <h2 className="box">Everything You Need To Know About Valorant !</h2>
            </GlitchClip>
        </div>
    );
}

export default Box;