import React from 'react';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';
import imagekilljoy from '../../Images/Killjoy.webp';

function Killjoy() {
    return(
        <div className="main">
            <div className='main-2'>Killjoy</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagekilljoy} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    The genius of Germany. Killjoy secures the battlefield with ease using her arsenal of inventions. If the damage from her gear doesn't stop her enemies, her robots' debuff will help make short work of them.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Alarmbot', cost: '200 Creds' },
                                    {ability: 'Turret', cost: 'Free per round'},
                                    {ability: 'Nanoswarm', cost: '200 Creds' },
                                    {ability: 'Lockdown', cost: '7 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
            </Card>
        </div>
    )
}

export default Killjoy;