import React, { Component } from 'react';
import './Meta.css';
import { Tabs, Tab, Grid, Cell, Spinner } from 'react-mdl';
import Ranks from './Ranks/Ranks.js';
import GameModes from './GameModes/GameModes.js';
import BattlePass from './BattlePass/BattlePass'

class AllMeta extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
            if(this.state.activeTab === 0){
                return(
                    <div className="projects-grid">
                        <Ranks />
                    </div>
                )
            }
            else if(this.state.activeTab === 1){
                return(
                    <div className="projects-grid">
                        <GameModes />
                    </div>
                )
            }      
            else if(this.state.activeTab === 2){
                return(
                    <div className="projects-grid">
                        <BattlePass />
                    </div>
                )
            }
            else if(this.state.activeTab === 3){
                return(
                    <div className="projects-grid">
                        <Spinner />
                    </div>
                )
            }
    }

    render() { 
        return (
        <div className="category-tabs">
            <Tabs activeTab={ this.state.activeTab } onChange={(tabID) => this.setState({ activeTab: tabID })} ripple>
                <Tab>Ranks</Tab>
                <Tab>Game Modes</Tab>
                <Tab>Battle Pass</Tab>
                <Tab>Something</Tab>
            </Tabs>

            <section className='allmapbg'>
                    <Grid>
                        <Cell col={12}><div className="content">{this.toggleCategories()}</div></Cell>
                    </Grid>             
            </section>
        </div>
        );
    }
}

export default AllMeta;