import React from 'react';
import './AgentInfo.css';
import {CardTitle, Card, CardActions, CardText, DataTable, TableHeader} from 'react-mdl';

import imagejett from '../../Images/Jett_artwork.webp';
function Jett() {
    return(
                <div className="main">
                    <div className='main-2'>Jett</div>
                    <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                        <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagejett} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                        <CardText>
                            Representing her home country of South Korea, Jett's agile and evasive fighting style lets her take risks no one else can. She runs circles around every skirmish, cutting enemies before they even know what hit them.
                        </CardText>
                        <CardActions>
                            <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Tailwind', cost: 'Free per round'},
                                    {ability: 'Updraft', cost: '200 Creds'},
                                    {ability: 'Cloudburst', cost: '100 Creds'},
                                    {ability: 'Blade Storm', cost: '7 Orbs'}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                        </CardActions>
                    </Card>
                </div>
    );
}

export default Jett;