import React from 'react';
import './GameModes.css';

function GameModes() {
    return(
        <div>
            <h3>Game Modes</h3>

            <div className='gamemodes'>
                Standard Plant/Defuse
                <div>
                    <h5>Plant/Defuse is a Gamemode where you play as an Attacker or a Defender, The Attacker objective is to plant the spike while the Defender objective is to prevent the Attacker to plant the spike. In this mode you will have the ability to open the shop, the shop is where you spend the creds that you get from killing the enemies, the creds will give you the ability to purchase Weapons, Shields and Abilities.</h5> 
                </div>
                    <div className='unranked'>
                        <b>Unranked</b>
                        <h6>Ranked Gamemode is where you play competitively against other player to get your placement in the Valorant Ranks.</h6>
                            <h6>This mode Last for 30-40 minutes</h6>
                    </div>
                    <div className='ranked'>
                        <b>Ranked</b>
                        <h6>Normal Gamemode is where you play Valorant for casually, or to test your skill without worrying about your Valorant rank placement.</h6>
                            <h6>This mode Last for 30-40 minutes</h6>
                    </div>
            </div>

            <div className='gamemodes'>
                Spike Rush
                    <div>
                        <h5>Spike Rush is a Gamemode where you play Plant/Defuse Gamemodes casually with other players. In a round where you will get random weapons and random orbs that will buff you/your teammate or debuff your enemies.</h5>
                            <h5>This mode Last for 8-12 minutes.</h5>
                    </div>
            </div>

            <div className='gamemodes'>
                Deathmatch
                    <div>
                        <h5>Deathmatch is a game mode where you will be placed with 14 different players in a randomly selected map, to test your skill with the gun of your choice from the shop.</h5>
                            <h5>This mode lasts for 9 minutes or until a player reaches 40 kills.</h5>
                            <h5>Abilities are disabled as agents are chosen automatically.</h5>
                    </div>
            </div>

            <div className='gamemodes'>
                Practice Game Modes
                    <div>
                        <h5>Practice Game Modes are a series of modes that you can play in the training grounds, this series of modes will let you have a taste of how Valorant games will work. </h5>
                        <div className='practicemodes'>
                        <b>Shooting Test</b>
                        <h6>Test your gun skills with a variety of shooting challenges.</h6>
                    </div>
                    <div className='practicemodes'>
                        <b>Spike Planting</b>
                        <h6>Take control of the site, plant the Spike, and defend until detonation.</h6>
                    </div>
                    <div className='practicemodes'>
                        <b>Spike Defuse</b>
                        <h6>Methodically defeat enemies and defuse the Spike before time runs out!</h6>
                    </div>
                    </div>
            </div>

        </div>
    );
}

export default GameModes;