import React from 'react';
import './MapsInfo.css';
import minimap from './Images/Bind_mini-map.webp';

function BindInfo() {
    return(
        <div className='maps'>
            Two sites. No middle. Gotta pick left or right. What’s it going to be then? Both offer direct paths for attackers and a pair of one-way teleporters make it easier to flank.
            <div className='minimap'><img src={minimap} style={{maxWidth:'50%'}} alt="minimap"/>
            </div>
        </div>
    )
}

export default BindInfo;