import Iron3 from './Images/TX_CompetitiveTier_Large_5.webp';
import Bronze1 from './Images/TX_CompetitiveTier_Large_6.webp';
import Bronze2 from './Images/TX_CompetitiveTier_Large_7.webp';
import Bronze3 from './Images/TX_CompetitiveTier_Large_8.webp';
import Silver1 from './Images/TX_CompetitiveTier_Large_9.webp';
import Silver2 from './Images/TX_CompetitiveTier_Large_10.webp';
import Silver3 from './Images/TX_CompetitiveTier_Large_11.webp';
import Gold1 from './Images/TX_CompetitiveTier_Large_12.webp';
