import React, { Component } from 'react';
import './Maps.css';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText,  CardMenu, } from 'react-mdl';

import AscentMap from './MapsInfo/AscentInfo';
import BindInfo from './MapsInfo/BindInfo';
import IceboxInfo from './MapsInfo/IceboxInfo';
import HavenInfo from './MapsInfo/HavenInfo';
import SplitInfo from './MapsInfo/SplitInfo';
import RangeInfo from './MapsInfo/RangeInfo';

import imageascent from './MapsInfo/Images/Loading_Screen_Ascent.webp';
import imagebind from './MapsInfo/Images/Loading_Screen_Bind.webp';
import imagehaven from './MapsInfo/Images/Loading_Screen_Haven.webp';
import imageicebox from './MapsInfo/Images/Loading_Screen_Icebox.webp';
import imagesplit from './MapsInfo/Images/Loading_Screen_Split.webp';
import imagerange from './MapsInfo/Images/Loading_Screen_Range.webp';

class AllMaps extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
            if(this.state.activeTab === 0){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageascent} style={{maxWidth:'100%'}} alt="Ascent"/></CardTitle>
                                <CardText>Ascent</CardText>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <AscentMap />
                    </div>
                )
            }
            else if(this.state.activeTab === 1){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagebind} style={{maxWidth:'100%'}} alt="Bind"/></CardTitle>
                                <CardText>Bind</CardText>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        < BindInfo />
                    </div>
                )
            }      
            else if(this.state.activeTab === 2){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagehaven} style={{maxWidth:'100%'}} alt="Haven"/></CardTitle>
                                <CardText>Haven</CardText>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        < HavenInfo />
                    </div>
                )
            }
            else if(this.state.activeTab === 3){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageicebox} style={{maxWidth:'100%'}} alt="Icebox"/></CardTitle>
                                <CardText>Icebox</CardText>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <IceboxInfo />
                    </div>
                )
            }
            else if(this.state.activeTab === 4){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagesplit} style={{maxWidth:'100%'}} alt="Split"/></CardTitle>
                                <CardText>Split</CardText>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <SplitInfo />
                    </div>
                )
            }else if(this.state.activeTab === 5){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagerange} style={{maxWidth:'100%'}} alt="Range"/></CardTitle>
                                <CardText>Range</CardText>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                        <RangeInfo />
                    </div>
                )
            }
    }

    render() { 
        return (
        <div className="category-tabs">
            <Tabs activeTab={ this.state.activeTab } onChange={(tabID) => this.setState({ activeTab: tabID })} ripple>
                <Tab>Ascent</Tab>
                <Tab>Bind</Tab>
                <Tab>Haven</Tab>
                <Tab>Icebox</Tab>
                <Tab>Split</Tab>
                <Tab>Range</Tab>
            </Tabs>

            <section className='allmapbg'>
                    <Grid>
                        <Cell col={12}><div className="content">{this.toggleCategories()}</div></Cell>
                    </Grid>             
            </section>
        </div>
        );
    }
}

export default AllMaps;